.App {
  text-align: center;
  font-family: Questrial, sans-serif;
  font-size: 1.125rem;
  padding-bottom: 80px;
}

a {
  text-decoration: none;
  color: rgb(32, 129, 226);
}

.App>* {
  margin-bottom: 30px;
}

.App-logo {
  max-height: 200px;
  pointer-events: none;
}

div.title {
  font-size: 1.35em;
  font-weight: 600;
  margin-bottom: 15px;
}

button {
  color: white;
  font-size: 0.9em;
  background: rgb(248, 113, 113);
  border-radius: 100px;
  padding: 10px 20px;
  margin: 15px 0;
  font-weight: 500;
  cursor: pointer;
  border: none;
  box-shadow: 1px 2px 1px rgb(0 0 0 / 10%);
}

button:disabled {
  background: rgb(179, 179, 179) !important;
  cursor: not-allowed;
}

button:hover {
  background: rgb(189, 74, 74);
}

div.nft-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}

div.nft-wrapper {
  border: 5px solid #EEE;
  border-radius: 5px;
  margin: 10px;
  padding: 5px;
}

div.nft-content {
  width: 200px;
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
}

div.nft-content>img {
  width: 200px;
  height: 200px;
}

div.nft.selected>.nft-wrapper {
  border-color: rgb(248, 113, 113)
}

div.nft-name {
  font-size: 0.9em;
}
